.main-header-menu-icon-popover-container {
	width: 250px;
}

.main-header-menu-icon-popover-list {
	position: fixed;
	top: 70px;
	left: 30px;
	width: 30%;
	z-index: 1000;
	-ms-flex-align: center;
	    align-items: center;
	background-color: #e6e6e6;
}

.main-header-menu-icon-popover-list div {
	height: 30px;
	background-color: #fff;
	color:#4d4d4d;
	padding: 5px;
	font-size: 1em;
	text-align: center;
	line-height: 30px;
	border-bottom: 1px solid #e6e6e6;
	-webkit-box-shadow: 0 5px 5px 0;
	        box-shadow: 0 5px 5px 0;
}

.main-header-menu-icon-popover-list div:hover {
    -webkit-transform: scale(1.3);
    -ms-transform: scale(1.3);
    transform: scale(1.3);
}


