.main-header-icon {
  width: 48px;
  height: 48px;
  margin: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  cursor: pointer;
}

.main-header-icon:active {
  opacity: 0.8;
  outline: 1px solid #fff;
}

.main-header-no--select {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}


